import React from "react"
import Layout from "../components/layout"
import Header from '../components/header'
import Post from   '../components/post'

import { Link, graphql } from "gatsby"
import Img from "gatsby-image"
import styled from "@emotion/styled"
import { css } from "@emotion/core"
import design from '../design'

const Wrapper = styled.div`
  padding: 20px;
`

export default ({ data }) => {
  console.log(data)
  return (
    <Layout>
        <Header/>
        <Wrapper>
        {data.posts.edges.sort((a,b)=>{return a.post_number - b.post_number}).map(({ node }) => (
          <Post className="post" post={node} key={node.name}/>
        ))}
        </Wrapper>
    </Layout>
  )}

  export const query = graphql`
  query MyQuery {
    posts: allFile(filter: {extension: {regex: "/yaml/"}}, sort: {fields: childPostsYaml___post_number, order: DESC}) {
      edges {
        node {
          id
          name
          childPostsYaml {
            author
            body
            post_number
            title
            type
            media {
              path {
                id
                childImageSharp {
                   fluid(maxWidth:1000, quality: 100) {
                     ...GatsbyImageSharpFluid
                     presentationWidth
                  }
                }
              }
              alt
            }
          }
        }
      }
    }
  }
  `
