import React from "react"
import { Link } from "gatsby"
import Img from "gatsby-image"

import styled from "@emotion/styled"
import { css } from "@emotion/core"
import design from '../design'

// const design = {
//   mainColor: '#a9313f',
//   bgColor: '#ffece2'
// }

const CPost = styled.div `
  display:block;
  float: none;
  padding: 20px;
  border: 1px solid ${design.mainColor};
  margin-bottom: 10px;

  p {
    white-space: pre-line;
    line-height: 1.3em;
    em {
      font-style: italic;
    }
  }

  p.body{
    margin-bottom: 10px;
  }

  p.title, p.author{
    &:before {
      content:'- '
    }
  }

  .images {
    margin-top:20px;
  }

  .gatsby-image-wrapper{
    max-width: 400px;
    height: auto;
    width: 100%;
    float: left;
    display: block;
    margin-bottom: 10px;
    margin-right: 10px;
    &:last-child{
      margin-right:0;
    }
  }
  &::after {
  content: "";
  clear: both;
  display: table;
}
`

function Post(props) {
  const post = props.post;
  return (
    <CPost className="post" key={props.post.name}>
      <p className="body">{props.post.childPostsYaml.body}</p>
      <p className="title">From <em>{props.post.childPostsYaml.title}</em></p>
      <p className="author">By <em>{props.post.childPostsYaml.author}</em></p>
      <div className="images">
        {props.post.childPostsYaml.media.map(img => (
          <Img key={img.path.id} fluid={img.path.childImageSharp.fluid} alt={img.alt}/>
        ))}
      </div>
    </CPost>
  )
}
export default Post
